import { createStore } from "vuex";
import data from "../assets/projects";

const store = createStore({
  state: {
    mainMenuItems: ["projects", "about me"],
    mainProjects: [1, 3, 4],
    projects: data.projects,
    targetComponent: "home",
    detailProject: 1,
    overlayPicture: {
      src: "",
      alt: "",
    },
    easterEgg: false,
  },
  getters: {
    getMainProjects: (state) => {
      return state.projects.filter((project) => {
        return state.mainProjects.includes(project.id);
      });
    },
    // getProject es per borrar
    getProject: (state) => (id) => {
      return state.projects.filter((project) => project.id === id)[0];
    },
    getSelectedProject: (state) => {
      return state.projects.filter((project) => {
        return project.id == state.detailProject;
      })[0];
    },
  },
  mutations: {},
  actions: {},
  modules: {},
});

export default store;
