<script>
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default {
  name: "ProjectDetail",
  props: {
    project: {
      type: Object,
      required: true,
    },
  },
  mounted: function () {
    this.scrollAnimation();
  },
  methods: {
    scrollAnimation() {
      window.scrollTo({ top: 0, behavior: "smooth" });
      setTimeout(function () {
        const parallax = document.querySelector(".parallax");
        const parallaxRect = parallax.getBoundingClientRect();
        const parallaxPictureHeight =
          document.querySelector(".parallax-picture").offsetHeight;
        const depth = document.querySelector(".parallax-picture").dataset.depth;
        const movement = (parallaxRect.height - parallaxPictureHeight) * depth;

        const tl = gsap.timeline({
          scrollTrigger: {
            trigger: ".parallax",
            scrub: 0.4,
            pin: false,
            start: "top " + parallax.offsetTop,
            end: "bottom " + parallaxRect.height,
            markers: false,
            invalidateOnRefresh: true,
          },
        });
        tl.to(".parallax-picture", {
          bottom: movement,
          ease: "none",
        });
      }, 800);
    },
  },
};
</script>
